

span.mdl-layout-title{
  font-family: "Brush Script MT", "Brush Script Std", cursive;
  font-size: 30px;
}


.avatar-img{
  height: 450px;
  padding-top: 3em;
  margin-top: 70px;
}

.banner-text{
  width :75%;
  margin:auto;
  border-radius:10px;
  padding-top: 1em;
  height: 400px;
  margin-top: 50px;

}

.banner-text h1{
  font-size: 25px;
  font-weight: bold;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  margin-top: 0px;
  margin-bottom: 0px;
}

.banner-text h1 span{
  font-size: 50px;
}

.banner-text h4{
  font-size: 13px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;

}

.banner-text hr{
  border-top: 2px dotted black;
  text-align: center;
  width:98%;
  margin-bottom: 1em;
  
}

.icon {
  display: flex;
  justify-content: space-around;
  margin: auto;

}

.contact{
  margin-top: 28px;
  margin-left: 10px;

}

.aboutme{
  margin-top: 200px;
}

/* Hover */

.mdl-layout__inner-container a.mdl-navigation__link:hover{
  background-color: #28A745;
  color: white;

}

.mdl-layout__inner-container a.mdl-navigation__link{
  color: black;
}

/* Drawer */
.mdl-layout__inner-container .mdl-layout__header .mdl-layout__drawer-button{
 color: black;
 display: none;
}

/* Phone size (Responsive Hearder) */
@media only screen and (max-width: 550px){
  .mdl-layout__inner-container .mdl-layout__header .mdl-layout__drawer-button{
    display:block;
  }
  .mdl-layout__inner-container a.mdl-navigation__link{
    display: none;
  }
  .avatar-img{
    height: 400px;
    width: 100%;
    margin-top: 0px;
  }


}